export default async function AccessPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = await params;
  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">Доступ и роли — {id}</h3>
      <p>Управление пользователями, ролями, ключами и X.509 сертификатами.</p>
    </div>
  );
}

